import React from 'react'
import Link from 'gatsby-link'
import styled from 'styled-components'
import breakpoint from 'styled-components-breakpoint'

import {Button} from '../utils/styled-components.js'
import {translate} from "react-i18next"

//styles

const Form = styled.form`
  background-color: ${props => props.theme.color.po};
  display: flex;
  flex-flow: column;
  padding: 1.45rem;
  border-radius: .25rem;
  color: white;
  width: 18rem;
  ${props => breakpoint('sm', props.theme.breakpoints)`
    width: 22rem;
  `}
`
const Input = styled.input`
  border-radius: .25rem;
  margin-right: 20%;
  transition: ${props => props.theme.effects.transition};
  font-size: .9rem;
  border: none;
  padding: .25rem;
  margin-bottom: .25rem;
  &:focus{
    padding: .5rem;
    margin-right: 0;
    outline: none;
    box-shadow: ${props => props.theme.effects.shadow[0]};
  }
`
const TextArea = styled.textarea`
  border-radius: .25rem;
  border: none;
  margin-right: 20%;
  transition: .3s ease;
  font-size: .9rem;
  padding: .25rem;
  resize: none;
  margin-bottom: .25rem;
  &:focus{
    padding: .5rem;
    margin-right: 0;
    outline: none;
    box-shadow: ${props => props.theme.effects.shadow[0]};
    height: 6rem;
  }
`
const SubmitButton = Button.extend`
  max-width: 6rem;
  justify-content: center;
  align-self: center;
  &:hover{
    color: ${props => props.theme.color.p};
    background-color: white;
  }
`
const Label = styled.label`
  padding: .25rem 0;
`
const ContactWrapper = styled.div`
  display: flex;
  flex-flow: column;
  align-items: center;
`

class ContactForm extends React.Component{
  constructor(props){
    super(props)
    this.state = {
      name: '',
      organization: '',
      phone: '',
      email: '',
      subject: '',
      message: '',
      isSubmit: false,
      isSuccess: null
    }
  }
  handleSubmit(){
    e.preventDefault();
    const formData = new FormData();
    formData.append("name", this.state.name)
    formData.append("organization", this.state.organization)
    formData.append("phone", this.state.phone)
    formData.append("_replyto", this.state.email)
    formData.append("subject", this.state.subject)
    formData.append("message", this.state.message)
    const req = new XMLHttpRequest();
    req.open('POST', "https://formspree.io/" + this.props.contactEmail);
    req.onreadystatechange = ()=>{
      (req.readyState == XMLHttpRequest.DONE && req.status == 200) ?
      this.setState({isSubmit: true, isSuccess: true}) :
      this.setState({isSubmit: true, isSuccess: false})
    }
    req.send(formData);
  }
  render(){
    const t = this.props.t;

    return(
      <Form>
        <Label htmlFor="name">{t('contact.form.name')}</Label>
        <Input id="name" placeholder="ie: Jane Doe" required/>
        <Label htmlFor="organization" >{t('contact.form.organization')}</Label>
        <Input htmlForm="organization" placeholder="ie: Doe Middle School"/>
        <Label htmlFor="email" >{t('contact.form.email')}</Label>
        <Input type="email" id="email" placeholder="ie: jane.doe@gmail.com" required />
        <Label htmlFor="phone" >{t('contact.form.phone')}</Label>
        <Input type="tel" id="phone" placeholder="(123) 456-7890" />
        <Label htmlFor="subject" >{t('contact.form.subject')}</Label>
        <Input id="subject" placeholder="Your Subject" required/>
        <Label htmlFor="message" >{t('contact.form.message')}</Label>
        <TextArea id="message" placeholder="Your message here" required/>
        <br/>
        <SubmitButton onClick={this.handleSubmit}>{t('contact.form.submit')}</SubmitButton>
      </Form>
    )
  }
}

export default translate()(ContactForm)



