import React from 'react'
import Link from 'gatsby-link'
import styled from 'styled-components'
import breakpoint from 'styled-components-breakpoint'

import ContactForm from '../components/ContactForm.js'
import {Button, PhotoWrapper, PageHeader, TextWrapper} from '../utils/styled-components.js'

// import { I18nextProvider } from "react-i18next"
import { translate } from "react-i18next"


// import i18n from '../utils/i18n.js'

//styles


const PhotoWrapperContact = PhotoWrapper.extend`
  background-image:   linear-gradient(rgba(0,0,0,0.45), rgba(0,0,0,0.45)),
  url('http://res.cloudinary.com/ds0f4qyyt/image/upload/s--Apiy0pSL--/c_scale,q_jpegmini,w_3773/v1493834434/london_f0prw6.jpg');
`

const Label = styled.label`

`
const ContactWrapper = styled.div`
  display: flex;
  flex-flow: column;
  align-items: center;
`


const RegistrationPage = ({data, t}) => {
  console.log(`404 loaded: ${t("key")}`);
  return(
  <ContactWrapper>
    <PhotoWrapperContact/>
    <PageHeader>{t("registration.header")}</PageHeader>
    <TextWrapper>
      <h4
      dangerouslySetInnerHTML={{__html: t('registration.subheader')}}
      style={{textAlign: 'center', lineHeight: "1.2rem"}}>
      </h4>
      <Link
      style={{textAlign: 'center', display: "block", lineHeight: "1.2rem"}}
      to="/contact/"><Button>{t("registration.contact")}</Button></Link>
    </TextWrapper>
  </ContactWrapper>
)}

export default translate('translation')(RegistrationPage)



